.content{
    margin: 0 auto;
}

.image{
    text-align:center;
}

.text{
    font-size: 17px;
    opacity: 0.6;
    margin: 0;
}

.button{
    position: relative;
    margin: 30px 0 0 0;
    width: 100%;
    height: 55px;
    background-color: #9dd558;
    border-radius: 18px;
    border: 0;
    color: #ffffff;
    font-size: 16px;
    font-weight: 500;
    cursor: pointer;
    transition: background-color 0.2s ease-in-out;
}
  
.button:hover{
   background-color: rgb(255, 73, 73);
}
  
.button:hover img{
    transform: translateX(-5px);
    transition: transform 0.15s ease-in-out;
}

.button img{
    position: absolute;
    left: 30px;
    top: 20px;
}