.favorite{
    position: absolute;
    cursor: pointer;
  }
  
  .card{
    display: flex;
    flex-direction: column;
    border: 1px solid  #eaeaea;
    padding: 30px;
    width: 250px;
    border-radius: 40px;
    margin: 0 30px 30px 0;
    transition: box-shadow 0.2s ease-in-out, transform 0.2s ease-in-out;
  }
  .imageFavorite{
    height: 30px;
    width: 30px;
    left: 175px;
    top: 1305px;
    border-radius: 7px;
    border: 1px solid  #eaeaea;

  }
  .imageItem{
    display: block;
    margin-left: auto;
    margin-right: auto
  }
  .card:hover{
    box-shadow: 0px 20px 35px rgba(182, 0, 76, 0.2);
    transform: translateY(-5px);
  }
  
  .cardBottom{
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  
  .cardPrice{
   display: flex;
   flex-direction: column;
  }
  
  .card h5 {
    text-align: center;
    flex: 1;
    font-weight: 400;
    font-size: 14px;
    font-weight: 500;
  }
  
  .card span {
    font-size: 16px;
    opacity: 0.5;
    text-transform: uppercase;
  }
  .card b {
    font-size: 16px;
  }
  
  .imagePlus{
    cursor: pointer;
  }